.products {
	position: relative;
	width: 100%;

	&__title {
		margin-top: 80px - 20px;
	}

	&__list {
		display: flex;
		flex-flow: row wrap;
		margin-right: -10px;
		margin-left: -10px;
		padding-bottom: 53px;

		@media (max-width: 1100px) {
			margin-right: 0;
			margin-left: 0;
		}
	}

	&__show-all {
		display: block;
		margin: 0 auto;
		border: 1px solid $color-chelsea-cucumber;
		border-radius: 4px;
		padding: 20px 50px;
		font-size: 14px;
		line-height: 1;
		background-color: transparent;
		transition: color 0.3s, background-color 0.3s;

		@media (min-width: 1024px) {
			&:focus,
			&:hover {
				outline: none;
				color: $color-white;
				background-color: $color-chelsea-cucumber;
				transition: color 0.3s, background-color 0.3s;
			}
		}
	}
}

.products-item {
	margin-top: 0;
	margin-bottom: 1px;
	padding-top: 46px;
	width: 33.33%;
	text-align: center;
	opacity: 1;
	transition: margin-top 0.7s, opacity 1.5s;

	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 64px;
	}

	&__title {
		margin-top: 25px;
		margin-bottom: 12px;
		font-size: 18px;
	}

	&__text {
		margin: 0;
		padding: 0 43px;
		font-size: 14px;
	}

	&--hidden {
		margin-top: -100%;
		opacity: 0;
		visibility: hidden;
		transition: margin-top 1s, opacity 0.5s;
	}

	@media (max-width: 680px) {
		width: 50%;
	}

	@media (max-width: 480px) {
		width: 100%;
	}
}