.about {
	position: relative;
	width: 100%;

	&__list {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		margin-right: -10px;
		margin-left: -10px;

		// @media (max-width: 1100px) {
		// 	margin-right: 0;
		// 	margin-left: 0;
		// }
	}
}

.about-item {
	margin: 0 10px;
	margin-bottom: 20px;
	padding-top: 30px;
	width: 340px;
	height: 323px;
	text-align: center;
	background-color: $color-alabaster;

	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 85px;
	}

	&__title {
		overflow: hidden;
		margin-top: 15px;
		margin-bottom: 0;
		font-size: 18px;
		white-space: nowrap;
		text-overflow: ellipsis;
	}

	&__text {
		overflow: hidden;
		margin-top: 0;
		padding: 10px 35px;
		height: 100px;
		font-size: 14px;
		line-height: 1.3;
		text-align: left;
	}

	&__link {
		position: relative;
		display: inline-block;
		margin-top: 5px;
		font-size: 14px;
		text-decoration: none;

		&::after {
			content: "";
			position: absolute;
			left: 0;
			bottom: 1px;
			width: 100%;
			height: 1px;
			background-color: $color-tory-blue;
			transition: left 0.3s, width 0.3s;
		}

		@media (min-width: 1024px) {
			&:focus,
			&:hover {
				&::after {
					left: 50%;
					width: 0;
					transition: left 0.3s, width 0.3s;
				}
			}
		}
	}

	@media (max-width: 1440px) {
		margin: 0 10 / 1440 * 100vw;
		margin-bottom: 20 / 1440 * 100vw;
		width: 31%;
	}

	@media (max-width: 768px) {
		width: 340px;
	}

	@media (max-width: 700px) {
		margin-bottom: 20px;
	}

	@media (max-width: 375px) {
		width: 300px;
	}
}