.intro {
	width: 100%;
	text-align: center;
	color: $color-white;
	background-image: url("../images/main-bg.jpg");
	background-size: cover;
	background-repeat: no-repeat;

	&__wrapper {
		padding-top: 52px;
		padding-bottom: 83px;
	}

	&__title {
		font-family: $font-open-sans;
		font-weight: 300;
		font-size: 48px;
		line-height: 58px;

		span {
			display: block;
		}
	}

	&__description {
		padding-top: 48px;
		padding-right: 8%;
		padding-left: 8.2%;
		font-weight: 400;
		font-size: 18px;
	}
}