*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-family: $font-pt-sans;
	font-size: 16px;
}

h1 {
	font-family: $font-open-sans;
}

a {
	color: $color-tory-blue;

	&:focus,
	&:hover {
		text-decoration: none;
	}
}

.wrapper {
	width: 100%;
	height: 100%;
	min-width: 100vw;
	min-height: 100vh;
}

.container {
	margin: 0 auto;
	width: 100%;
	max-width: 1060px;

	@media (max-width: 1024px) {
		padding: 0 15px;
	}
}

.article-title {
	margin-top: 80px;
	margin-bottom: 32px;
	width: 100%;
	font-size: 18px;
	text-align: center;
}