.navigation {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-between;
	width: 1060px - 249px;

	&__list {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		list-style: none;
		margin: 0;
		padding-bottom: 1px;
		padding-left: 31px;

		@media (max-width: 1024px) {
			flex-flow: column nowrap;
			padding-left: 0;
			width: 100%;
		}
	}

	&__item {
		position: relative;
		padding: 0 30.3px;
		line-height: 1;

		&:hover,
		&.open {
			> .navigation__dropdown {
				top: 100%;
				overflow-y: scroll;
				max-height: 100px;
				opacity: 1;
				visibility: visible;
				transition: top 0.3s, opacity 1s;

				@media (max-width: 1024px) {
					position: relative;
					left: auto;
					overflow: hidden;
					width: 100%;
					max-height: 100%;
					text-align: center;
				}
			}

			> .navigation__link-arrow {
				transform: rotate(180deg) translateY(2px);
				transition: transform 0.3s;

				@media (max-width: 1024px) {
					right: auto;
					top: 17px;
					margin-left: 5px;
					transform: rotate(180deg) translateY(-4px);
				}
			}
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		padding: 10px 0;
		line-height: 1;
		text-decoration: none;
		color: $color-white;
		transition: color 0.3s;

		&:hover,
		&:focus {
			text-shadow: 1px 1px 2px rgba($color-black, 0.9);
			color: darken($color-white, 10%);
			transition: color 0.3s;
		}

		@media (max-width: 1024px) {
			padding: 15px 0;
		}
	}

	&__link-arrow {
		position: absolute;
		right: 14px;
		top: 50%;
		width: 10px;
		height: 5px;
		fill: $color-white;
		transition: transform 0.3s;

		@media (max-width: 1024px) {
			display: inline-block;
			vertical-align: middle;
		}
	}

	&__dropdown {
		position: absolute;
		left: 30px;
		top: 0;
		z-index: 1;
		display: flex;
		flex-direction: column;
		list-style: none;
		margin: 0;
		padding-left: 0;
		width: max-content;
		text-align: left;
		opacity: 0;
		visibility: hidden;
		transition: top 0.3s, opacity 1s;
		scrollbar-width: none;

		&-item {
			padding: 10px 0;

			@media (max-width: 1024px) {
				padding: 15px 0;
			}
		}

		&-link {
			text-decoration: none;
			text-shadow: 1px 1px 2px rgba($color-black, 0.9);
			color: $color-white;
			transition: color 0.3s;

			&:hover,
			&:focus {
				color: darken($color-white, 15%);
				transition: color 0.3s;
			}
		}

		&::-webkit-scrollbar {
			display: none;
		}

		&:hover {
			top: 100%;
			opacity: 1;
			visibility: visible;
			transition: top 0.3s, opacity 1s;
		}
	}

	&__buttons {
		padding-bottom: 2px;
		font-size: 0;

		@media (max-width: 1024px) {
			width: 100%;
		}
	}

	&__btn {
		display: inline-block;
		vertical-align: middle;
		font-size: 16px;
		text-decoration: none;
		color: $color-white;
		transition: color 0.3s;

		&:not(:first-child) {
			padding-left: 30px;
		}

		&:hover,
		&:focus {
			text-shadow: 1px 1px 2px rgba($color-black, 0.9);
			color: darken($color-white, 10%);
			transition: color 0.3s;
		}

		@media (max-width: 1024px) {
			display: block;
			margin: 0 auto;
			padding: 15px 0;

			&:not(:first-child) {
				padding-left: 0;
			}
		}
	}

	@media (max-width: 1024px) {
		position: fixed;
		right: -100%;
		top: 0;
		bottom: 0;
		z-index: 2;
		flex-flow: column nowrap;
		justify-content: center;
		width: 60vw;
		height: 100vh;
		background-color: $color-big-stone;
		transition: right 0.8s;

		&.active {
			right: 0;
			transition: right 0.8s;
		}
	}

	@media (max-width: 425px) {
		width: 100vw;
	}
}