.partners {
	position: relative;
	padding-bottom: 113px;
	width: 100%;

	&__descr {
		&:not(:first-child) {
			margin-top: 22px;
		}
	}

	&__images {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		padding-top: 10px;
		padding-left: 12px;

		@media (max-width: 480px) {
			flex-flow: column nowrap;
			padding-left: 0;
		}
	}

	&__img {
		transition: opacity 0.3s;

		img {
			height: auto;
			max-width: 100%;
		}

		&:not(:last-child) {
			padding-right: 35px;

			@media (max-width: 480px) {
				padding-right: 0;
			}
		}

		@media (max-width: 480px) {
			&:not(:last-child) {
				margin-bottom: 25px;
			}
		}

		@media (min-width: 1024px) {
			&:hover {
				opacity: 0.6;
				transition: opacity 0.3s;
			}
		}
	}

	@media (max-width: 768px) {
		padding-bottom: 80px;
	}
}