.page-header {
	padding-top: 24px;
	width: 100%;

	&__wrapper {
		display: flex;
		flex-flow: row nowrap;
	}

	&__logo {
		display: inline-block;
		vertical-align: middle;
		width: 249px;
	}

	&__burger-btn {
		position: fixed;
		right: 15px;
		top: 28px;
		z-index: 10;
		display: block;
		margin: 0;
		border: none;
		padding: 0;
		width: 40px;
		height: 40px;
		background-color: transparent;

		&:hover,
		&:focus {
			outline: none;
		}

		@media (min-width: 1025px) {
			display: none;
		}
	}

	&__burger {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 10;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		margin: auto;
		padding: 10px 5px;
		width: 40px;
		height: 40px;
		background-color: rgba($color-big-stone, 0.4);

		> span {
			display: block;
		}

		&.active {
			background-color: transparent;

			.page-header__burger-top {
				width: 28px;
				transform: rotate(135deg) translate(7.5px, -6px);
			}

			.page-header__burger-bottom {
				width: 28px;
				transform: rotate(-135deg) translate(0.5px, -1px);
			}

			.page-header__burger-footer {
				top: 0;
				opacity: 0;
				transform: rotate(180deg);
			}
		}

		@media (min-width: 1025px) {
			display: none;
		}
	}

	&__burger-top {
		position: relative;
		border: none;
		border-radius: 40px;
		width: 30px;
		height: 3px;
		background: $color-white;
		transition: transform 0.5s, top 0.2s;
	}

	&__burger-bottom {
		position: relative;
		border: none;
		border-radius: 40px;
		width: 30px;
		height: 3px;
		background: $color-white;
		transition: transform 0.5s, top 0.2s;
		transition-delay: 0.2s, 0s;
	}

	&__burger-footer {
		position: relative;
		border: none;
		border-radius: 40px;
		width: 30px;
		height: 3px;
		background: $color-white;
		transition: all 0.5s;
		transition-delay: 0.1s;
	}
}