.page-footer {
	position: relative;
	padding-top: 30px;
	padding-bottom: 42px;
	width: 100%;
	background-color: $color-big-stone;

	&__nav {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
	}

	&__nav-list {
		list-style: none;
		padding-left: 0;
		font-size: 0;
		text-align: center;
	}

	&__nav-item {
		display: inline-block;
		vertical-align: middle;
		font-size: 16px;
	}

	&__nav-link {
		position: relative;
		display: inline-block;
		padding: 10px 12px;
		text-decoration: none;
		text-transform: uppercase;
		color: $color-white;

		&::after {
			content: "";
			position: absolute;
			left: 50%;
			bottom: 10px;
			width: 0;
			height: 1px;
			background-color: transparent;
			transition: width 0.3s, left 0.3s;
		}

		@media (min-width: 1024px) {
			&:focus,
			&:hover {
				&::after {
					left: 12px;
					width: calc(100% - 24px);
					background-color: $color-white;
					transition: width 0.3s, left 0.3s;
				}
			}
		}
	}

	&__social {
		list-style: none;
		padding-left: 5px;
		font-size: 0;
		text-align: center;
	}

	&__social-item {
		display: inline-block;
		vertical-align: middle;
		padding: 17px;
	}

	&__social-link {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		width: 50px;
		height: 50px;
		background-color: rgba($color-white, 0.3);
		transition: background-color 0.3s;

		svg {
			fill: $color-white;
			transition: transform 0.3s;
		}

		&--fb {
			svg {
				width: 16px;
				height: 31px;
			}
		}

		&--tw {
			svg {
				width: 30px;
				height: 25px;
			}
		}

		&--inst {
			svg {
				width: 32px;
				height: 32px;
			}
		}

		@media (min-width: 1024px) {
			&:focus,
			&:hover {
				background-color: transparent;
				transition: background-color 0.3s;

				svg {
					transform: scale(1.2);
					transition: transform 0.3s;
				}
			}
		}
	}

	&__copyright {
		margin: 0;
		padding: 12px 0;
		width: 100%;
		font-weight: 700;
		font-size: 15px;
		text-align: center;
		color: $color-white;
	}
}